@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,300;1,400&display=swap);
.listItemWrapper {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    margin-bottom:25px;
}
.listItemImage {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-right: 10px;
    
}
.ListItemName {
    margin: 0;
    color: rgb(2, 72, 223);
}
.ListItemDescription {
    padding: 0;
}
.ListItemButton {
    background-color: transparent;
    border-radius: 0%;
    border: solid rgb(2, 72, 223) 2px;
    color: rgb(2, 72, 223);
    padding: 6px 11px;
    transition: ease-in-out 1s;
}
.ListItemButton:hover {
    background-color: rgb(2, 72, 223);
    border: solid rgb(2, 72, 223) 2px;
    border-radius: 7%;
    color: white;
    padding: 6px 11px;
}
body {
    padding: 0;
    margin: 0;
    font-family: 'Montserrat',sans-serif;
}
*,*::before,*::after {
    box-sizing: border-box;
}

